.app_header {
	grid-area: app_header;
	margin: 0;
	padding: 10px;
	display: flex;
	align-items: center;
	justify-content: space-between;
	// background: rgb(150, 192, 213);
	// background: rgb(127, 255, 202);
	width: 100%;
	background: #fff;
	background: rgb(220, 235, 255);
	// background: rgb(220, 255, 185);
	border: 1px solid rgba(0, 0, 0, 0.1);
	border-bottom-left-radius: 5px;
	border-bottom-right-radius: 5px;
	// p {
	// 	margin: 0;
	// 	padding: 0;
	// 	display: inline-block;
	// 	vertical-align: middle;
	// 	width: 90%;
	// 	text-align: center;
	// 	margin-left: calc(0px - 100px / 2);
	// 	font-weight: bold;
	// 	font-size: clamp(1.5em, 5vw, 2.5em);
	// }
	&_logo {
		margin: 0;
		padding: 0;
		display: block;
		max-width: 50px;
	}
	&_loginBlock a {
		margin: 0;
		padding: 0;
		display: flex;
		align-items: center;
		justify-content: flex-start;
		text-align: center;
		font-weight: bold;
		text-decoration: none;
		color: inherit;
		line-height: 100%;
	}
	&_loginTrue {
		display: flex;
		align-items: center;
		justify-content: flex-start;
	}
	.avatar {
		display: block;
		margin: 0;
		padding: 0;
		max-width: 40px;
		width: 100%;
		margin-right: 10px;
		border-radius: 50%;
	}
	.logout {
		display: block;
		margin: 0;
		padding: 0;
		max-width: 18px;
		width: 100%;
		margin-left: 10px;
		margin-top: 3px;
		cursor: pointer;
		opacity: 0.5;
		&:hover {
			opacity: 1;
		}
	}
}
